___CSS_LOADER_EXPORT___.push([module.id, `.advantages {
    margin-top: 130px;
}

.slideshow {
    width: 100%;
    height: 600px;
    position: relative;
    overflow: hidden;
    background: #000;
    margin: 20px 0;
}

.slideshow-item {
    width: 100%;
    height: 100%;
    position: absolute;
    opacity: 0;
    animation: slideanim 40s infinite;
    pointer-events: none;
}

.slideshow-item:nth-child(1),
.slideshow-item:nth-child(1) img {
    animation-delay: 0;
}

.slideshow-item:nth-child(2),
.slideshow-item:nth-child(2) img {
    animation-delay: 10s;
}

.slideshow-item:nth-child(3),
.slideshow-item:nth-child(3) img {
    animation-delay: 20s;
}

.slideshow-item:nth-child(4),
.slideshow-item:nth-child(4) img {
    animation-delay: 30s;
}

.slideshow-item img {
    width: 100%;
    height: 100%;
    object-fit: cover;
    animation: zoom 40s infinite;
}

.slideshow-item-text {
    max-width: 50%;
    position: absolute;
    bottom: 20px;
    left: 20px;
    background-color: rgba(0, 0, 0, 0.7);
    color: #fff;
    padding: 20px 30px;
    font-family: Verdana, sans-serif;
}

.slideshow-item-text h5 {
    font-size: 22px;
    margin: 0 0 10px 0;
    color: #BFE2FF;
}

.slideshow-item-text p {
    font-size: 15px;
    margin-bottom: 10px;
}

@keyframes slideanim {
    12.5% {
        opacity: 1;
        pointer-events: auto;
    }

    25% {
        opacity: 1;
        pointer-events: auto;
    }

    37.5% {
        opacity: 0;
    }
}

@keyframes zoom {
    50% {
        transform: scale(1.3);
    }
}`, "",{"version":3,"sources":["webpack://./src/style/blocks/main_page_advantages.css"],"names":[],"mappings":"AAAA;IACI,iBAAiB;AACrB;;AAEA;IACI,WAAW;IACX,aAAa;IACb,kBAAkB;IAClB,gBAAgB;IAChB,gBAAgB;IAChB,cAAc;AAClB;;AAEA;IACI,WAAW;IACX,YAAY;IACZ,kBAAkB;IAClB,UAAU;IACV,iCAAiC;IACjC,oBAAoB;AACxB;;AAEA;;IAEI,kBAAkB;AACtB;;AAEA;;IAEI,oBAAoB;AACxB;;AAEA;;IAEI,oBAAoB;AACxB;;AAEA;;IAEI,oBAAoB;AACxB;;AAEA;IACI,WAAW;IACX,YAAY;IACZ,iBAAiB;IACjB,4BAA4B;AAChC;;AAEA;IACI,cAAc;IACd,kBAAkB;IAClB,YAAY;IACZ,UAAU;IACV,oCAAoC;IACpC,WAAW;IACX,kBAAkB;IAClB,gCAAgC;AACpC;;AAEA;IACI,eAAe;IACf,kBAAkB;IAClB,cAAc;AAClB;;AAEA;IACI,eAAe;IACf,mBAAmB;AACvB;;AAEA;IACI;QACI,UAAU;QACV,oBAAoB;IACxB;;IAEA;QACI,UAAU;QACV,oBAAoB;IACxB;;IAEA;QACI,UAAU;IACd;AACJ;;AAEA;IACI;QACI,qBAAqB;IACzB;AACJ","sourcesContent":[".advantages {\r\n    margin-top: 130px;\r\n}\r\n\r\n.slideshow {\r\n    width: 100%;\r\n    height: 600px;\r\n    position: relative;\r\n    overflow: hidden;\r\n    background: #000;\r\n    margin: 20px 0;\r\n}\r\n\r\n.slideshow-item {\r\n    width: 100%;\r\n    height: 100%;\r\n    position: absolute;\r\n    opacity: 0;\r\n    animation: slideanim 40s infinite;\r\n    pointer-events: none;\r\n}\r\n\r\n.slideshow-item:nth-child(1),\r\n.slideshow-item:nth-child(1) img {\r\n    animation-delay: 0;\r\n}\r\n\r\n.slideshow-item:nth-child(2),\r\n.slideshow-item:nth-child(2) img {\r\n    animation-delay: 10s;\r\n}\r\n\r\n.slideshow-item:nth-child(3),\r\n.slideshow-item:nth-child(3) img {\r\n    animation-delay: 20s;\r\n}\r\n\r\n.slideshow-item:nth-child(4),\r\n.slideshow-item:nth-child(4) img {\r\n    animation-delay: 30s;\r\n}\r\n\r\n.slideshow-item img {\r\n    width: 100%;\r\n    height: 100%;\r\n    object-fit: cover;\r\n    animation: zoom 40s infinite;\r\n}\r\n\r\n.slideshow-item-text {\r\n    max-width: 50%;\r\n    position: absolute;\r\n    bottom: 20px;\r\n    left: 20px;\r\n    background-color: rgba(0, 0, 0, 0.7);\r\n    color: #fff;\r\n    padding: 20px 30px;\r\n    font-family: Verdana, 