___CSS_LOADER_EXPORT___.push([module.id, `.header {
    position: relative;
}

body {
    background-image: url(${___CSS_LOADER_URL_REPLACEMENT_0___});
        background-position: top;
        background-size: cover;
        background-repeat: no-repeat;
}

.flat__title {
    margin-top: 20px;
    font-weight: 900;
    font-size: clamp(25px, 3.333vw, 64px);
    text-align: center;
    color: var(--color-dark-blue);
}

.grid-container {
    display: grid;
    margin-top: 20px;
    grid-template-columns: repeat(3, 1fr);
    grid-auto-flow: 300px;
    gap: 38px;

    &>div:nth-child(n+2) {
        width: 100%;
        height: 300px;
    }

    & img {
        width: 100%;
        height: 100%;
        object-fit: cover;
    }
}

.first {
    grid-column: 1 / 4;
    max-width: 1060px;
    max-height: 689px;
    margin: 0 auto;
}

.swiper {
    display: none;
}

.descr {
    margin-top: 40px;
    color: var(--color-dark-blue);
}

.descr__wraper {
    display: flex;
    flex-wrap: wrap;
}

.descr__left {
    display: flex;
    flex-direction: column;
    width: 70%;
}

.descr__left-block-wrapper {
display: flex;
}

.ds-flex {
    display: flex;
}


.descr__block {
    display: flex;
    justify-content: center;
    align-items: baseline;
    text-align: center;
    margin-right: 10px;
    padding-right: 5px;
    font-weight: 400;
    font-size: 20px;
    color: var(--color-dark-blue);

    svg {
        margin-right: 10px;
        display: inline-block;
        margin-block: auto;
    }

    p {
        display: inline-block;
        margin-block: auto;
        transform: translateY(10%);
    }
}

.descr__block:not(:last-child) {
    border-right: 1px solid var(--color-dark-blue);
}

.descr__left-block-wrapper-text {
    margin-top: 40px;
    padding-right: 20px;
}

.descr__left-block-text {
    font-size: 22px;
}

.descr__rigth {
    width: 30%;
}

.descr__rigth-block {
    display: flex;
    align-items: baseline;
    column-gap: 12px;
    font-weight: 400;
    font-size: 20px;
}

.ds-none {
    display: none;
}

.plan {
    margin-top: 40px;
    margin-bottom: 40px;
}

.plan__title {
    text-align: center;
    color: var(--color-dark-blue);
    font-size: 35px;
}

.plan img {
    margin-top: 20px;
    object-fit: cover;
    width: 100%;
    aspect-ratio: 1;
}`, "",{"version":3,"sources":["webpack://./src/layout_pages_style/layout_pages_main.css"],"names":[],"mappings":"AAAA;IACI,kBAAkB;AACtB;;AAEA;IACI,yDAA+C;QAC3C,wBAAwB;QACxB,sBAAsB;QACtB,4BAA4B;AACpC;;AAEA;IACI,gBAAgB;IAChB,gBAAgB;IAChB,qCAAqC;IACrC,kBAAkB;IAClB,6BAA6B;AACjC;;AAEA;IACI,aAAa;IACb,gBAAgB;IAChB,qCAAqC;IACrC,qBAAqB;IACrB,SAAS;;IAET;QACI,WAAW;QACX,aAAa;IACjB;;IAEA;QACI,WAAW;QACX,YAAY;QACZ,iBAAiB;IACrB;AACJ;;AAEA;IACI,kBAAkB;IAClB,iBAAiB;IACjB,iBAAiB;IACjB,cAAc;AAClB;;AAEA;IACI,aAAa;AACjB;;AAEA;IACI,gBAAgB;IAChB,6BAA6B;AACjC;;AAEA;IACI,aAAa;IACb,eAAe;AACnB;;AAEA;IACI,aAAa;IACb,sBAAsB;IACtB,UAAU;AACd;;AAEA;AACA,aAAa;AACb;;AAEA;IACI,aAAa;AACjB;;;AAGA;IACI,aAAa;IACb,uBAAuB;IACvB,qBAAqB;IACrB,kBAAkB;IAClB,kBAAkB;IAClB,kBAAkB;IAClB,gBAAgB;IAChB,eAAe;IACf,6BAA6B;;IAE7B;QACI,kBAAkB;QAClB,qBAAqB;QACrB,kBAAkB;IACtB;;IAEA;QACI,qBAAqB;QACrB,kBAAkB;QAClB,0BAA0B;IAC9B;AACJ;;AAEA;IACI,8CAA8C;AAClD;;AAEA;IACI,gBAAgB;IAChB,mBAAmB;AACvB;;AAEA;IACI,eAAe;AACnB;;AAEA;IACI,UAAU;AACd;;AAEA;IACI,aAAa;IACb,qBAAqB;IACrB,gBAAgB;IAChB,gBAAgB;IAChB,eAAe;AACnB;;AAEA;IACI,aAAa;AACjB;;AAEA;IACI,gBAAgB;IAChB,mBAAmB;AACvB;;AAEA;IACI,kBAAkB;IAClB,6BAA6B;IAC7B,eAAe;AACnB;;AAEA;IACI,gBAAgB;IAChB,iBAAiB;IACjB,WAAW;IACX,eAAe;AACnB","sourcesContent":[".header {\r\n    position: relative;\r\n}\r\n\r\nbody {\r\n    background-image: url(../img/main-bg/body.webp);\r\n        background-position: top;\r\n        background-size: cover;\r\n        background-repeat: no-repeat;\r\n}\r\n\r\n.flat__title {\r\n    margin-top: 20px;\r\n    font-weight: 900;\r\n    font-size: clamp(25px, 3.333vw, 64px);\r\n    text-align: center;\r\n    color: var(--color-dark-blue);\r\n}\r\n\r\n.grid-container {\r\n    display: grid;\r\n    margin-top: 20px;\r\n    grid-template-columns: repeat(3, 1fr);\r\n    grid-auto-flow: 300px;\r\n    gap: 38px;\r\n\r\n    &>div:nth-child(n+2) {\r\n        width: 100%;\r\n        height: 300px;\r\n    }\r\n\r\n    & img {\r\n        width: 100%;\r\n        height: 100%;\r\n        